export default class SessionStorageUtil {

  private static readonly true = "true";
  private static readonly false = "false";

  public static enable(key: string): void {
    sessionStorage.setItem(key, this.true);
  }

  public static disable(key: string): void {
    sessionStorage.setItem(key, this.false);
  }

  public static isEnable(key: string): boolean {
    const val = sessionStorage.getItem(key);
    return val === this.true;
  }
}