import { useEffect } from "react";
import { ControlButtons } from "../containers";
import { useControlButtonsProps, controlButtonsInitValue } from "../containers/useControlButtons";

const useSetHeader = (props?: useControlButtonsProps, trigger?: any) => {
  const { visibleControlButtons, setVisibleControlButtons } = ControlButtons.useContainer();

  useEffect(() => {
    // Set props.
    if (trigger !== null) {
      if (props) {
        setVisibleControlButtons({
          ...controlButtonsInitValue,
          ...props
        });
      } else {
        setVisibleControlButtons({
          ...visibleControlButtons,
          ...controlButtonsInitValue
        });
      }
    }
  }, [trigger]);

}

export default useSetHeader;
