import React, { memo } from "react";
import styled from "styled-components";
import { Commentary, CommentaryProps } from "../../atoms/Commentary/Commentary";

export interface CommentaryUnitProps {
  commentaries: CommentaryProps[];
}

/**
 * Styled component.
 */
const Main = styled.div`
  margin-bottom: 50px;
  &:last-child {
    margin-bottom: 0;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const CommentaryUnit: React.FC<CommentaryUnitProps> = memo(({
  commentaries
}) => {
  return (
    <>
      { commentaries.map((x, num) => 
        <Main key={ num }>
          <Commentary
            content={ x.content }
            title={ x.title }
            image={ x.image }
          >
            { x.children }
          </Commentary>
        </Main>
      ) }
    </>
  );
});