import { useState, ReactElement } from "react";
import { LightHouseButtonProps } from "../components/atoms/LightHouseButton/LightHouseButton";
import { PageProgressProps } from "../components/atoms/PageProgress/PageProgress";
import { TransProps } from "../components/atoms/Trans";

export type notation = ReactElement | string;
export interface useControlButtonsProps {

  prevHref?: string;
  prevTransitionKey?: TransProps["transitionKey"];
  prevClick?: () => void;
  prevCallBack?: () => void;
  prevNotation?: notation;

  nextHref?: string;
  nextTransitionKey?: TransProps["transitionKey"];
  nextClick?: () => void;
  nextCallBack?: () => void;
  nextNotation?: notation;

  nextHref2?: string;
  nextTransitionKey2?: TransProps["transitionKey"];
  nextClick2?: () => void;
  nextCallBack2?: () => void;
  nextNotation2?: notation;

  color?: LightHouseButtonProps["color"];
  isExistTower?: boolean;

  pageProgress?: {
    currentPage: PageProgressProps["currentPage"];
    pageAmount: PageProgressProps["pageAmount"];
  };
}

export const controlButtonsInitValue: useControlButtonsProps = {

  prevHref: void(0),
  prevTransitionKey: "init",
  prevClick: void(0),
  prevCallBack: void(0),
  prevNotation: "",

  nextHref: void(0),
  nextTransitionKey: "init",
  nextClick: void(0),
  nextCallBack: void(0),
  nextNotation: "",

  nextHref2: void(0),
  nextTransitionKey2: "init",
  nextClick2: void(0),
  nextCallBack2: void(0),
  nextNotation2: "",

  color: void(0),
  isExistTower: true,

  pageProgress: void(0)
};

export default () => {
  const [visibleControlButtons, setVisibleControlButtons]
    = useState<useControlButtonsProps>(controlButtonsInitValue);
  return {
    visibleControlButtons, setVisibleControlButtons
  };
}
