import React, { memo } from "react";
import styled from "styled-components";
import { MainTextSourceHanSansJp } from "../../../styles/MainText"

export interface CommentaryProps {
  content: string;
  title: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  children: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  image: string;
}

/**
 * Styled component.
 */
const circleW = 80;
const Main = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Image = styled.div<{
  image: CommentaryProps["image"]
}>`
  width: ${ circleW }px;
  height: ${ circleW }px;
  background-image: url(/${ props => props.image });
  background-size: cover;
  background-repeat: no-repeat;
`;

const Comment = styled.div`
  width: calc(100% - ${ circleW }px);
  padding-left: 20px;
`;

const Title = styled(MainTextSourceHanSansJp)<{
  content: CommentaryProps["content"]
}>`
  margin-bottom: 16px;
  &:before {
    content: "${ props => props.content }";
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const Commentary: React.FC<CommentaryProps> = memo(({
  content,
  title,
  children,
  image
}) => {

  const fl = 24;
  const fm = 18;

  return (
    <>
      <Main>
        <Image image={ image } />
        <Comment>
          <Title
            content={ content }
            color="#F53636"
            fontSize={ {
              pc: fl,
              tb: fl,
              sp: fl
            } }
          >
            { title }
          </Title>
          <MainTextSourceHanSansJp
            fontSize={ {
              pc: fm,
              tb: fm,
              sp: fm
            } }
          >
            { children }
          </MainTextSourceHanSansJp>
        </Comment>
      </Main>
    </>
  );
});
