import Head from "next/head";
import React from "react";
import useSetHeader from "../../../../hooks/useSetHeader";
import Ruby from "../../../../components/atoms/Ruby/Ruby";
import { CheckEnd } from "../../../../components/templates/CheckEnd/CheckEnd";
import useCatchStorage from "../../../../hooks/useCatchStorage";
import storageKeys from "../../../../constants/storageKeys";
import notations from "../../../../constants/notations";
import { controlButtonsInitValue } from "../../../../containers/useControlButtons";

export default function Page({
}) {

  const isInOrder = useCatchStorage(storageKeys.isInOrder);

  useSetHeader({
    nextHref: isInOrder ? "/menus/menu2/quiz" : "/menus/menu2/main",
    nextTransitionKey: isInOrder ? "fade" : "slideAsVerticalPrev",
    nextNotation: isInOrder ? "◯×クイズ" : notations.end,

    nextHref2: isInOrder ? "/menus/menu3/act/1" : controlButtonsInitValue["nextHref2"],
    nextTransitionKey2: isInOrder ? "fade" : controlButtonsInitValue["nextTransitionKey2"],
    nextNotation2: isInOrder ? "メニュー３へ" : controlButtonsInitValue["nextNotation2"],

    color: "orange"
  }, isInOrder);

  return (
    <>
      <Head>
        <title>キケンを探せ！海のチェックポイント</title>
      </Head>
      <CheckEnd
        image="kiken_menu2_illust.png"
        commentaries={ [
          {
            content: "①",
            title: <>ぼうはてい</>,
            children: <><Ruby ruby="みず">水</Ruby>の<Ruby ruby="なが">流</Ruby>れがふくざつで、<Ruby ruby="ふか">深</Ruby>くなっていることがある。</>,
            image: "kiken_menu2_icon_1.png"
          },
          {
            content: "⑤",
            title: <><Ruby ruby="かいすいよくじょう">海水浴場</Ruby>の<Ruby ruby="そと">外</Ruby></>,
            children: <><Ruby ruby="ふか">深</Ruby>くて<Ruby ruby="なみ">波</Ruby>の<Ruby ruby="つよ">強</Ruby>い<Ruby ruby="ばしょ">場所</Ruby>もあるが、かんし<Ruby ruby="いん">員</Ruby>さんがいない。ボートなどが<Ruby ruby="とお">通</Ruby>ることもある。</>,
            image: "kiken_menu2_icon_5.png"
          },
          {
            content: "⑥",
            title: <><Ruby ruby="かこう">河口</Ruby></>,
            children: <><Ruby ruby="かわ">川</Ruby>の<Ruby ruby="みず">水</Ruby>は<Ruby ruby="からだ">体</Ruby>が<Ruby ruby="う">浮</Ruby>きにくく、<Ruby ruby="つめ">冷</Ruby>たい。<Ruby ruby="うみ">海</Ruby>に<Ruby ruby="お">押</Ruby>し<Ruby ruby="だ">出</Ruby>す<Ruby ruby="なが">流</Ruby>れも<Ruby ruby="つよ">強</Ruby>い。</>,
            image: "kiken_menu2_icon_6.png"
          }
        ] }
      />
    </>
  )
}
