import styled, { css } from "styled-components";
import { heiseiMaruGothicStdW8, sourceHanSansJpBold } from "./fonts";
import customMedia from "./customMedia";
import colors from "../constants/colors";
import DependsSize from "../Types/DependsSize";
import TextAlignProps from "../Types/TextAlignProps";

export interface MainTextProps {
  fontSize: DependsSize["size"];
  color?: string;
  lineHeight?: number;
  textAlign?: TextAlignProps["textAlign"];
}

/**
 * Styled component.
 */
const common = css<MainTextProps>`
  color: ${ props => props.color ? props.color :  colors.navy };
  line-height: ${ props => props.lineHeight ? props.lineHeight : 2 };
  text-align: ${ props => props.textAlign };
  word-break: break-all;
  ${ customMedia.greaterThan<MainTextProps>("tb")`
    font-size: ${ props => props.fontSize.pc }px;
  ` };
  ${ customMedia.lessThan<MainTextProps>("tb")`
    font-size: ${ props => props.fontSize.tb }px;
  ` };
  ${ customMedia.lessThan<MainTextProps>("sp")`
    font-size: ${ props => props.fontSize.sp }px;
  ` };
`;

export const MainTextHeiseiMaruGothic = styled.div`
  ${ common }
  ${ heiseiMaruGothicStdW8 }
`;

export const MainTextSourceHanSansJp = styled.div`
  ${ common }
  ${ sourceHanSansJpBold }
`;