import React, { memo } from "react";
import styled from "styled-components";
import { sidePadding } from "../../../styles/wrapper";
import size from "../../../constants/size";
import useHeaderHeight from "../../../hooks/useHeaderHeight";

export interface HeaderOvrProps {
}

/**
 * Styled component.
 */
export interface IHeaderOvrStyles {
  height: number;
}

const Main = styled.a.attrs<IHeaderOvrStyles>(
  (props) => ({
    style: {
    }
  })
)<IHeaderOvrStyles>`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  ${ sidePadding }
  z-index: 1;
  &:before {
    content: "";
    background-color: #fff;
    padding-top: ${ props => props.height }px;
    display: block;
    margin: 0 auto;
    max-width: ${ size.contentMaxWidth }px;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const HeaderOvr: React.FC<HeaderOvrProps> = memo(({
}) => {
  const height = useHeaderHeight();
  return (
    <>
      <Main height={ height } />
    </>
  );
});
