import { useEffect, useState } from "react";
import SessionStorageUtil from "../util/SessionStorageUtil";

const useCatchStorage = (key: string) => {
  const [isExist, setIsExist] = useState<boolean>(null);

  useEffect(() => {
    const isEnable = SessionStorageUtil.isEnable(key);
    setIsExist(isEnable);
  }, []);

  return isExist;
}

export default useCatchStorage;
